<style>
  .add-address .container{
    width: 95%;
    margin: 0 auto;
  }
</style>
<template>
  <div class="add-address">
    <div class="container">
      <mt-field placeholder="收货人" v-model="address.name"></mt-field>
      <mt-field placeholder="邮编" v-model="address.postcode"></mt-field>
      <mt-field placeholder="省市区" v-model="address.district"></mt-field>
      <mt-field placeholder="详细地址" type="textarea" rows="4" v-model="address.detail"></mt-field>
      <mt-button size="large" type="primary" @click="save">保存</mt-button>
    </div>
  </div>
</template>

<script>
import { Toast } from 'mint-ui';
export default {
  data(){
    return {
      address:{
        name: "",
        postcode: "",
        district: "",
        detail: ""
      }
    }
  },
  methods:{
    save(){
      var obj = this.address
      if(obj.name == "" || obj.postcode == "" || obj.district == "" || obj.detail == ""){
        Toast("请输入正确的收货地址")
        return
      }
      if(!localStorage.address){
        var arr = []
        arr.push(obj)
        localStorage.address = JSON.stringify(arr)
      }else{
        var arr = JSON.parse(localStorage.address)
        arr.push(obj)
        localStorage.address = JSON.stringify(arr)
        Toast({
          message: '添加成功'
        })
        this.$router.go(-1)
      }
    }
  }
}
</script>